<template>
  <div class="about">
    <vx-header :showback="1" :title="$t('settings.about')"></vx-header>
    <div class="vx-container">
      <div class="logo"></div>
      <div class="br">{{$t('common.version')}} {{version}}</div>
      <div class="detail">
        {{$t('common.vcanDetail')}}
      </div>
      <div class="list bt">{{$t('common.email')}} ：support@novastar.tech</div>
      <div class="list">{{$t('common.web')}}：
        <span v-if="getLang == 'zh'">www.novastar-led.cn</span>
        <span v-else>www.novastar.tech</span>
      </div>
      <div class="erm">
        <div class="weixin">
          <img src="../assets/common/weixin.png" alt=""> {{$t('common.weixin')}}
        </div>
        <div class="facebook">
          <img src="../assets/common/facebook.png" alt=""> Facebook
        </div>
      </div>
    </div>
    <div class="footer">
      Copyright © 2018 Xi’an NovaStar Tech Co., Ltd. All rights reserved.
    </div>
  </div>
</template>

<script>
  import { mapGetters } from 'vuex';
  export default {
    data() {
      return {
        version: ''
      };
    },
    created() {
      this.version = window.Android.getVersionName();
    },
    computed: {
      ...mapGetters(['getLang'])
    }
  };
</script>


<style lang="less" scoped>
  .vx-container {
    background-color: #383940;
  }
  .logo {
    margin-top: 35px;
    height: 160px;
    width: 100%;
    background: ~"url('../assets/common/vcan.png') no-repeat" #383940;
    background-position: center center;
    background-size: 160px 160px;
  }
  .br {
    font-size: 30px;
    color: #ddd;
    padding-top: 15px;
    text-align: center;
  }
  .detail {
    font-size: 28px;
    color: #ddd;
    padding: 45px 30px;
    text-align: left;
    line-height: 40px;
  }
  .bt {
    border-top: 2px solid #626262;
  }
  .list {
    padding: 0 30px;
    height: 80px;
    line-height: 80px;
    font-size: 28px;
    color: #ddd;
    text-align: left;
    border-bottom: 2px solid #626262;
    a {
      text-decoration: none;
      color: #ddd;
    }
  }
  .erm {
    margin: 80px 0;
    height: 290px;
    color: #ddd;
    font-size: 28px;
    display: flex;
    justify-content: center;
    .weixin {
      width: 220px;
      height: 290px;
      img {
        width: 220px;
        height: 220px;
        padding-bottom: 10px;
      }
      float: left;
    }
    .facebook {
      width: 220px;
      height: 290px;
      margin-left: 14%;
      img {
        width: 220px;
        height: 220px;
        padding-bottom: 10px;
      }
      float: right;
    }
  }
  .footer {
    position: fixed;
    bottom: 0;
    height: 80px;
    width: 100%;
    color: #ddd;
    text-align: center;
    font-size: 24px;
    z-index: 3;
  }
</style>



